import { useState } from "react"
import seriesStatusInterface from "../../helpers/interfaces/seriesStatusInterface"
import useStatusAPI from "../../hooks/useStatusAPI"
import BackHome from "./Back/BackHome"
import HiatusHome from "./Hiatus/HiatusHome"

const Home: React.FC = () => {
	const [status, setStatus] = useState<seriesStatusInterface>()
	const [isLoading, setIsLoading] = useState(false);
	useStatusAPI(setStatus, setIsLoading)
	if (!status?.onHiatus) {
		return (
			<BackHome />
		)
	}
	else {
		return <>
			<HiatusHome status={status} />
		</>
	}


}

export default Home