import HiatusHeader from '../../Header/Hiatus/hiatusHeader'
import './style.css'
import HiatusBody from '../../Body/Hiatus/Hiatus'
import seriesStatusInterface from '../../../helpers/interfaces/seriesStatusInterface'
import background from '../../../static/backgrounds/hiatus/hiatusBackground.svg'
import waveBackground from '../../../static/backgrounds/hiatus/wave.svg'
import { useEffect } from 'react'
const Home = ({ status }: Props) => {
	useEffect(() => {
		changeBodyColor()
	})
	const changeBodyColor = () => {
		document.body.style.backgroundColor = '#596b5d'
		if (window.matchMedia("(min-width: 500px)").matches) {
			document.body.style.backgroundImage = `url(${background})`
		}
	}
	return (
		<div id="hiatusHome">
			<HiatusHeader />
			<HiatusBody status={status} />
		</div>
	)
}

interface Props {
	status: seriesStatusInterface
}



export default Home