import seriesStatusInterface from "../../../helpers/interfaces/seriesStatusInterface"
import './style.css'
import gon from '../../../static/backgrounds/hiatus/gon.png'
import { slideInLeft, fadeIn } from 'react-animations'
import Radium from 'radium';
const HiatusBody = ({ status }: Props) => {

	const styles: any = {
		slideInLeft: {
			animation: 'x 1s',
			animationName: Radium.keyframes(slideInLeft, 'slideInLeft')
		},
		fadeIn: {
			animation: 'x 3s',
			animationName: Radium.keyframes(fadeIn, 'fadeIn'),
		}

	}

	return (
		<div id="hiatusBody">
			<div>
				<Radium.StyleRoot style={styles.fadeIn}>
					<p className="hiatus-text">THE GREAT HIATUS ARC <br />
						CONTINUES AS OF ISSUE {status.issue}</p>
				</Radium.StyleRoot>
				<Radium.StyleRoot style={styles.slideInLeft}>
					<hr id="line" />
				</Radium.StyleRoot>
				<Radium.StyleRoot style={styles.fadeIn}>
					<p className="hiatus-text">While you are at it, check out more<br />cool stuff about the manga!</p>
				</Radium.StyleRoot>
			</div>
		</div>
	)
	//				<img src={gon}></img>

}

interface Props {
	status: seriesStatusInterface
}


export default HiatusBody