import { useEffect } from "react";
import seriesStatusInterface from "../helpers/interfaces/seriesStatusInterface";
import { getSeriesStatus } from "../services/api";
function useStatusAPI(setStatus: React.Dispatch<React.SetStateAction<seriesStatusInterface | undefined>>, setIsLoading: React.Dispatch<React.SetStateAction<boolean>>){
	useEffect(() => {
		async function fetchStatus(){
			setIsLoading(true)
			setStatus(await getSeriesStatus())
			setIsLoading(false)
		}
		fetchStatus()
	}, [setStatus, setIsLoading])
}

export default useStatusAPI