import logo from '../../../static/backgrounds/hiatus/logo.png'
import CSS from 'csstype'
import HeaderItem from '../../HeaderItem/HeaderItem'
import './hiatusHeaderStyle.css'

const HiatusHeader = () => {
	return (
		<div id="hiatusHeader">
			<img id="logo" src={logo}></img>
			<div id="hiatusItems">
				<HeaderItem text="Hiatus Table" backgroundColor="white" fontColor="#596b5d" fontSize="0.5em"/>
				<HeaderItem text="Author Comments" backgroundColor="white" fontColor="#596b5d" fontSize="0.5em"/>
				<HeaderItem text="About" backgroundColor="white" fontColor="#596b5d" fontSize="0.5em"/>
			</div>

		</div>
	)
}

export default HiatusHeader