import CSS from 'csstype'
import backBackground from '../../../static/backgrounds/back/backBackground.svg'
import {createStore} from 'redux'
import statusReducer from '../../../reducers/statusReducer'
import renderApp from '../../../helpers/renderApp'
const Home = () => {
	const store = createStore(statusReducer)
	store.subscribe(() => {
		renderApp()
	})
	return(
		<div style={homeStyle}>
		</div>
	)
}

const homeStyle: CSS.Properties = {
	backgroundImage: `url(${backBackground})`,
	height: '100%',
	backgroundSize: 'cover',
}


export default Home